


import { Component, Vue } from "vue-property-decorator";
import axios from "axios";

@Component
export default class Register extends Vue {
  constructor() {
    super();
    this.onRedirect();
  }
  public name = "";
  public email = "";
  public password = "";
  public token = "";
  public register(): void {
    this.$swal.fire({
      html: "processing...",
      timer: 10000,
      allowOutsideClick: () => !this.$swal.isLoading(),
      onBeforeOpen: () => {
        this.$swal.showLoading();
        axios
          .post("http://api.sevadev.com/api/v1/register", {
            name: this.name,
            email: this.email,
            password: this.password
          })
          .then((response: any) => {
            this.token = response.data.token;
            if (response.data.auth) {
              this.$store.dispatch("login", this.token);
              this.$store.dispatch("register", response.data.user);
            }
            this.$swal.fire({
              icon: "success",
              title: "Register successfully",
              showConfirmButton: false,
              timer: 1500
            });
            this.onRedirect();
          })
          .catch(() => {
            this.$swal.fire({
              icon: "error",
              title: "Problem while registering",
              showConfirmButton: false,
              timer: 1500
            });
          });
      },
      onClose: () => {
        this.$swal.fire({
          icon: "error",
          title: "Problem while registering",
          showConfirmButton: false,
          timer: 1500
        });
      }
    });
  }

  public onRedirect(): void {
    if (this.$store.state.isAuthenticated) {
      this.$router.push({ name: "Start" });
    }
  }
}
