


import { Component, Vue } from "vue-property-decorator";
import axios from "axios";

@Component
export default class Login extends Vue {
  constructor() {
    super();
    this.onRedirect();
  }
  public email = "";
  public password = "";
  public token = "";
  public login(): void {
    this.$swal.fire({
      html: "Logging In...",
      timer: 10000,
      allowOutsideClick: () => !this.$swal.isLoading(),
      onBeforeOpen: () => {
        this.$swal.showLoading();
        axios
          .post("http://api.sevadev.com/api/v1/login", {
            email: this.email,
            password: this.password
          })
          .then((response: any) => {
            this.token = response.data.token;
            if (response.data.auth) {
              this.$store.dispatch("login", this.token);
              this.$store.dispatch("register", response.data.user);
            }
            this.$swal.fire({
              icon: "success",
              title: "Logged in successfully",
              showConfirmButton: false,
              timer: 1500
            });
            this.onRedirect();
          })
          .catch(() => {
            this.$swal.fire({
              icon: "error",
              title: "Try again",
              text: "Username or Password is wrong!"
            });
          });
      },
      onClose: () => {
        this.$swal.fire({
          icon: "error",
          title: "Try again",
          text: "Username or Password is wrong!"
        });
      }
    });
  }

  public skiplogin(): void {
    this.$store.dispatch("login", "token");
    this.onRedirect()
  }

  public onRedirect(): void {
    if (this.$store.state.isAuthenticated) {
      this.$router.push({ name: "Start" });
    }
  }
}
