import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isAuthenticated: false,
    token: "",
    user: {}
  },
  mutations: {
    isAuthenticate: (state, { authenticatation, token }) => {
      state.isAuthenticated = authenticatation;
      state.token = token;
    },
    isRegister: (state, { user }) => {
      state.user = user;
    }
  },
  actions: {
    login: (context, token) => {
      context.commit("isAuthenticate", { authenticatation: true, token });
    },
    logOut: context => {
      context.commit("isAuthenticate", { authenticatation: false, token: "" });
    },
    register: (context, user) => {
      context.commit("isRegister", { user: user });
    }
  }
});
