


import { Component, Vue } from "vue-property-decorator";
import axios from "axios";

@Component
export default class Game extends Vue {
  constructor() {
    super();
    this.onRedirect();
    this.resetAll();
  }
  public isGameOver = false;

  public word = "";
  public words: Array<string> = [];
  public currentTime = "1:00";
  public startTimer = false;
  public seconds = 60;
  public correctWord = 0;
  public totalWord = 0;

  public onSubmit(): void {
    const wordFromArray = this.words.shift();

    if (this.words.length > 0) {
      this.totalWord += 1;
      if (wordFromArray == this.word.trim()) {
        this.correctWord += 1;
      }
      this.word = "";
    } else {
      this.isGameOver = true;
    }
  }

  public logOut(): void {
    this.$store.dispatch("logOut");
    this.onRedirect();
  }

  public startTimerFunc(): void {
    const timer = window.setInterval(() => {
      if (this.seconds < 60) {
        this.currentTime = this.seconds.toString();
      }
      if (this.seconds > 0) {
        this.seconds--;
      } else {
        clearInterval(timer);
        this.isGameOver = true;
      }
    }, 1000);
  }

  public beginTime(): void {
    if (!this.startTimer) {
      this.startTimerFunc();
      this.startTimer = true;
    }
  }

  public resetAll(): void {
    this.word = "";
    this.isGameOver = false;
    this.seconds = 60;
    this.currentTime = "1:00";
    this.startTimer = false;
    this.correctWord = 0;
    axios
      .get("https://random-word-api.herokuapp.com/word?number=200")
      .then((response: any) => {
        this.words = response.data;
      })
      .catch(() => {
        this.$swal.fire({
          icon: "error",
          title: "Try later",
          text: "error while geeting words!"
        });
      });
  }

  public onRedirect(): void {
    if (!this.$store.state.isAuthenticated) {
      this.$router.push({ name: "Login" });
    }
  }
}
